<template>
  <div id="background">
    <div id="change-password-box">
      <h1><strong>修</strong>改密码</h1>
      <table>
        <tr>旧密码</tr>
        <tr><Input v-model="firstPassword" type="password" placeholder="请输入密码" style="width: 280px"></Input></tr>
        <tr>新密码</tr>
        <tr><Input v-model="secondPassword" type="password" placeholder="请输入新密码" style="width: 280px"></Input></tr>
        <tr>再次输入新密码</tr>
        <tr><Input v-model="secondPassword" type="password" placeholder="请再次输入新密码" style="width: 280px"></Input></tr>
        <tr><Button type="success" size="large" :disabled="password_box_submit_state" @click="submitPassword">提交</Button></tr>
        <div id="message-box">
          小提示: 密码必须大于八位数
        </div>
      </table>
    </div>
  </div>
</template>
<script>
export default {

}
</script>
<style>
#change-password-box {
  background-color: #FFFFFF;
  width: 50%;
  height: 600px;
  margin: 0 auto;
  margin-top: 30px;
  box-shadow:0 1px 2px rgba(0,0,0,0.3);
  border-radius: 5px;
}
#change-password-box table {
    margin: 0 auto;
    margin-top: 20px;
  }
#change-password-box tr {
  display: block;
  margin-bottom: 10px;
}
#message-box {
  margin-top: 20px;
  height: 60px;
}
#message-box p {
  line-height: 60px;
  font-size: 15px;
}
</style>


